form.login-component {
    margin: 20px;
    padding: 20px;
    border: 1px solid silver;
    border-radius: 10px;
    display:inline-block;
}
form.login-component div.login-flex {
    display:grid;
    grid-template-columns: repeat(2,1fr);
}
form.login-component label {
    display:block;
}
form.login-component input[type="submit"] {
    font-size: medium;
}

